<template>
  <div class="content">
    <div class="container-fluid">
      <div class="row">
        <div class="col-12">
          <card class="strpied-tabled-with-hover"
                body-classes="table-full-width table-responsive"
          >
            <template slot="header">
              <h4 class="card-title chart-title">Ордеры</h4>
            </template>
            <l-table class="table-hover table-striped"
                     :columns="tableColumns"
                     :data="tableData">
            </l-table>
          </card>
          <div style="display: flex; flex-direction: row;">
            <button @click="prevPage" v-if="offset != 1" style="margin-right: 10px; margin-bottom: 0px; margin-top: 0px;" class="btn btn-primary">
              {{ offset - 1 }}
            </button>
            <p style="display: flex; align-items: center; justify-content: center; text-align: center; margin: 0; margin-right: 10px;">
              {{ offset }}
            </p>
            <button v-if="offset != maxPage - 1" @click="nextPage" style="margin-top: 0px; margin-bottom: 0px;" class="btn btn-primary">
              {{ offset + 1 }}
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import LTable from 'src/components/Table.vue'
  import Card from 'src/components/Cards/Card.vue'
  import api from 'src/api.js'
  export default {
    components: {
      LTable,
      Card
    },
    watch: {
      offset: function() {
        this.getData()
      }
    },
    data() {
      return {
        maxPage: 1,
        offset: 1,
        tableColumns: ['date', 'number', 'status', 'account', 'RUB','USDT','user'],
        tableData: [
          {
            RUB: 0,
            USDT: 0,
            date: 0,
            number: 0,
            status: 0,
            account: 0,
            user: 0
          },
        ]
      }
    },
    mounted() {
      this.getData()
    },  
    methods: {
      nextPage() {
        this.offset++
      },
      prevPage() {
        this.offset -= 1
      },
      getData() {
        api.get(`https://dev.fly7.ru/mb600/hs/dash/orders?limit=20&offset=${this.offset}`).then(response => {
          this.tableData = response.data.results
          this.maxPage = response.data.total
        })
      }
    }
  }
</script>
